.tooltip {
  position: relative;
  display: inline-block;
  opacity: 1;
  width: 100%;
  padding: 0 !important;
  font-family: "Poppins", sans-serif !important;
  /*font-family: initial !important;
  font-size: initial !important;*/
}



.tooltip .tooltiptext {
  visibility: hidden;
  background-color: #898989;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 16px;
  opacity: 1000;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 36%;
  margin-left: -60px;
}

.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #898989 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}


